<template>
  <div>
    <div @click="openDialog">
      <slot></slot>
    </div>
    <v-dialog
      width="unset"
      v-model="settingsDialog"
    >
      <v-card>
        <v-icon small class="close-dialog" @click="settingsDialog = false">mdi-close</v-icon>
        <div class="settings-card">
          <h1 class="headline">Настройки аккаунта</h1>
          <div class="settings-wrapper">
            <!-- <v-file-input
              :rules="[value => !value || value.size < 2000000 || 'Вес картинки не должен превышать 2мб.']"
              accept="image/png, image/jpeg"
              placeholder="Загрузи аватар"
              prepend-icon="mdi-camera"
              label="Аватар"
              v-model="file"
              ></v-file-input> -->
            <v-text-field v-model="imageUrl" label="URL аватара"></v-text-field>
            <v-btn dense outlined :color="color" @click="post">
              <v-progress-circular
                v-if="loading"
                indeterminate
                color="grey"
                class="mr-2"
                size="18"
                width="2"
              ></v-progress-circular>
              <v-icon left v-if="!loading && success == null">mdi-upload</v-icon>
              <v-icon left v-if="!loading && success == true">mdi-check</v-icon>
              <v-icon left v-if="!loading && success == false">mdi-alert-circle-outline</v-icon>
              Загрузить
            </v-btn>
          </div>
        </div>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  const urlAvatar = '/api/v1/account/load_avatar/'

  export default {
    name: 'Settings',
    props: ['avatarUrl'],
    data () {
      return {
        settingsDialog: false,
        loading: false,
        success: null,
        color: 'primary',
        imageUrl: null
      }
    },
    methods: {
      openDialog () {
        setTimeout(() => { this.settingsDialog = true })
      },
      post () {
        if (this.imageUrl) {
          this.loading = true
          let r = new Promise((resolve, reject) => {
            axios.post(urlAvatar, {image_url: this.imageUrl})
              .then(response => {
                if (response.status === 200 && response.data.avatar_url !== 'error') {
                  resolve(true)
                } else {
                  reject(new Error('Loading avatar error'))
                }
              })
              .catch(error => {
                console.log(error)
                reject(new Error('Loading avatar error'))
              })
          })
          r.then(
            (result) => {
              this.loading = false
              this.success = true
              this.color = 'success'
              let self = this
              setTimeout(function () {
                self.$router.go()
              }, 300)
            },
            (error) => {
              console.log(error)
              this.loading = false
              this.success = false
              this.color = 'red lighten-1'
            }
          )
        }
      }
    }
  }
</script>

<style scoped>
  div .v-dialog {
    z-index: 999;
  }

  .headline {
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    margin-bottom: 1.3rem;
  }

  .settings-card {
    padding: 1rem 3rem;
  }

  @media screen and (max-width: 1000px) {
    .create-card {
      min-width: 0;
    }
  }

  .close-dialog {
    cursor: pointer;
    position: absolute;
    right: 1rem;
    top: 1rem;
  }
</style>



